.menu {
  background-color: $colorGrey;

  font-weight: 600;
  color: $darkGrey;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;
  }

  &-top {
    margin-bottom: 44px;
  }

  &__list {
    margin-bottom: 48px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    color: $darkGrey;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border color $-transform;

    &:hover {
      border-color: $darkGrey;
    }
  }

  &__call {
    display: block;
    width: max-content;

    text-transform: uppercase;
    color: $darkGrey;

    &--phone {
      font-size: 16px;
      line-height: 27px;

      margin-bottom: 16px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;

      padding-bottom: 8px;
      border-bottom: 1px solid $darkGrey;
    }
  }
}
